.highcharts-label .highcharts-tooltip {
  font-family: 'Avenir-Next-Regular', Verdana, Geneva, sans-serif;
  padding: 10px; }
  .highcharts-label .highcharts-tooltip span {
    font-family: 'Avenir-Next-Regular', Verdana, Geneva, sans-serif;
    color: #9aa5bc; }
  .highcharts-label .highcharts-tooltip .tooltip {
